<template>
<div :class="{ 'fixed-navbar': true, 'changed-navbar': bodyTop > 100 }">
  <div class="logo">
    <img src="../assets/Metology-logo_BEST.png" alt="">
  </div>
  <div class="navbar-items hidden-ms">
    <div class="navbar-item" v-for="item in items" @click="jumpto(item.target)">
      <span>{{ item.en }}</span>
      <p>{{ item.zh }}</p>
    </div>
  </div>
</div>
</template>

<script>
import jump from 'jump.js';

export default {
  name: 'Navbar',
  props: ['bodyTop'],
  data() {
    return {
      items: [
        {
          en: 'Brand Story',
          zh: '品牌故事',
          target: '#brand-story',
        },
        {
          en: 'Our product',
          zh: '產品介紹',
          target: '#our-product',
        },
        {
          en: 'About Debby',
          zh: '人工智慧衛教諮詢',
          target: '#about-debby',
        },
        {
          en: 'Our Team',
          zh: '我們的成員',
          target: '#our-team',
        },
        {
          en: 'Our Partners',
          zh: '合作夥伴',
          target: '#our-partners',
        },
        {
          en: 'Contact us',
          zh: '聯絡我們',
          target: '#contact-us',
        },
      ],
    };
  },
  methods: {
    jumpto(target) {
      jump(target, {
        offset: -100,
      });
    },
  },
};
</script>


<style lang="sass">
.fixed-navbar
  position: fixed
  width: 100vw
  top: 0
  z-index: 20
  height: 64px
  display: flex
  align-items: center
  @media screen and (max-width: 550px)
    justify-content: center
  .logo
    img
      height: 100%
      image-rendering: pixelated;
  .logo, .navbar-items, .navbar-item
    display: inline-block
    height: 100%
  .navbar-items
    margin-left: auto
    margin-right: 10px
  .navbar-item
    cursor: pointer
    margin-right: 24px
    padding-top: 12px
    color: white
    font-weight: 200
    &:before
      content: '';
      height: calc(100% - 32px);
      border: 1px solid;
      position: absolute;
      margin-left: -12px;
      margin-top: 6px
    p
      margin: 0px
      
.changed-navbar
  background-color: white
  box-shadow: 0 2px 2px 0 rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.2), 0 1px 5px 0 rgba(0,0,0,.12)
  .navbar-item
    color: black

.hidden-ms
  @media screen and (max-width: 550px)
    display: none !important

</style>




