<template>
<div class="chat-wrap">
  <div class="conversation" id="conversation">
    <div v-if="conversation.length !== 0">
      <div v-for="phrase in conversation">
        <div v-if="phrase.from === 'user'" class="chat chat-from-user">
          <div class="chat-text">{{ phrase.text }}</div>
        </div>
        <div v-else class="chat chat-from-debby">
          <img src="../assets/debby-small.jpg" height="64px" width="64px"/><div class="chat-text">{{ phrase.text.speech }}</div>
        </div>
      </div>
    </div>
    <div v-else>
    <p style="text-align: center; padding-top: 10px">----  Debby is online now! ----</p>
    </div>
  </div>
  <div class="conversation-input">
    <input type="text" v-model="userInput" @keypress="checkevent" placeholder="想跟Debby說些什麼呢？">
    <button @click="checkevent">送出</button>
  </div>
  <hr>
  <div class="add-friend-wrap">
    <div class="qr-wrap">
      <img src="http://qr-official.line.me/L/JCLZu9Eh21.png" height="100" width="100">
      <span>快用Line掃描QR Code 加 Debby 為好友！</span>
    </div>
    <a href="https://line.me/R/ti/p/%40olj5826z"><img height="36" border="0" alt="加入好友" src="https://scdn.line-apps.com/n/line_add_friends/btn/zh-Hant.png"></a>
  </div>
</div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'Conversation',
  data() {
    return {
      userInput: '',
      conversation: [],
    };
  },
  methods: {
    checkevent(event) {
      if (event.type === 'keypress') {
        console.log(event);
        if (event.keyCode === 13) {
          this.send();
        }
      }

      if (event.type === 'click') {
        this.send();
      }
    },
    send() {
      if (this.userInput !== '') {
        this.conversation.push({
          from: 'user',
          text: this.userInput,
        });

        const accessToken = 'c3b26e4ab2d744ee8b456b34a41f85cc';
        const baseUrl = 'https://api.api.ai/v1/';
        const text = this.userInput;
        axios.post(`${baseUrl}query?v=20150910`, {
          query: text,
          lang: 'zh-tw',
          sessionId: 'metology-web',
        }, {
          headers: {
            Authorization: `Bearer ${accessToken}`,
          },
        })
        .then((res) => {
          console.log(res);
          const messages = res.data.result.fulfillment.messages;
          messages.map((val) => {
            this.conversation.push(
              {
                from: 'debby',
                text: val,
              },
            );
            return 1;
          });

          this.userInput = '';
        });
      }
    },
  },
  updated() {
    const element = document.getElementById('conversation');
    element.scrollTop = element.scrollHeight - element.clientHeight;
  },
};
</script>

<style lang="sass">
.chat-from-user
  text-align: right
  .chat-text
    background-color: #0067aa
    color: white
    

.chat-from-debby
  display: flex
  .chat-text
    background-color: #ed6f05
    color: white

.chat
  margin: 10px
  padding: 10px 5px
  .chat-text
    padding: 10px
    border-radius: 5px
    display: inline-block

.conversation
  height: 400px
  background-color: white
  border: 1px solid rgba(0,0,0,0.2)
  overflow: auto
  border-radius: 10px

.conversation-input
  padding: 10px 0 
  input
    width: calc(100% - 94px)

.qr-wrap
  display: flex
  justify-content: center
  flex-flow: row;
  align-items: center;
  width: 70%
  span
    margin: 10px

.add-friend-wrap
  display: flex;
  justify-content: center;
  flex-flow: column;
  align-items: center;
  a
    margin-top: 20px

</style>




